<template>
        <v-col cols="12" xs="12" sm="12" md="8" lg="8" class="mx-auto">
            <v-breadcrumbs :items="items">
                <template v-slot:divider>
                    <v-icon>mdi-chevron-right</v-icon>
                </template>
            </v-breadcrumbs>
            <v-card class="__card">
                <v-card-title class="__card-title justify-center">
                    <p> ເພີ່ມ Portfolio</p>
                </v-card-title>
                <hr />
                <v-col cols="12" xs="12" md="10" lg="10"  class=" mx-auto">
                    <ValidationObserver ref="observer" v-slot="{ invalid }" slim>
                        <v-form @submit.prevent="addPortfolio" class="text-center">
                            <h2 class="font-weight-bold">ຂໍ້ມູນຂອງ Portfolio</h2>
                            <div class="uploader mb-5"
                                 @dragenter="OnDragEnter"
                                 @dragleave="OnDragLeave"
                                 @dragover.prevent
                                 @drop="onDrop"
                                 :class="{ dragging: isDragging }">
                                <i class="fas fa-cloud-upload-alt fa-3x"></i>
                                <p>ລາກຮູບມາໄວ້ບ່ອນນີ້...</p>
                                <div>ຫຼື</div>
                                <div class="file-input">
                                    <label for="file">ເລືອກຮູບ</label>
                                    <input @change="onInputChange" type="file" id="file"
                                           multiple>
                                </div>
                                <v-row>
                                    <v-col cols="4" xs="4" sm="4" md="3" lg="3" v-for="(image, index) in images" :key="index">
                                        <div class="image-wrapper">
                                            <v-img
                                                    contain
                                                    height="90"
                                                    class="text-right"
                                                    :src="image"
                                                    :lazy-src="image"
                                            >
                                                <i @click="spliceImage(index)" class="i fas fa-backspace mr-1 red--text" ></i>
                                            </v-img>
                                            <!--                  <div class="detail">-->
                                            <!--                    <span class="name" v-text="files[index].name"></span>-->
                                            <!--                    <span class="size" v-text="files[index].size"></span>-->
                                            <!--                  </div>-->
                                        </div>
                                    </v-col>
                                </v-row>
                            </div>
                            <ValidationProvider
                                    mode="lazy"
                                    name="ຫົວຂໍ້"
                                    rules="required|between: 4, 50"
                                    v-slot="{ errors }"
                            >
                                <v-text-field
                                        v-model="title"
                                        label="ຫົວຂໍ້"
                                        outlined
                                        clearable
                                        :error-messages="errors"
                                ></v-text-field>
                            </ValidationProvider>
                            <ValidationProvider
                                    mode="lazy"
                                    name="ລາຍລະອຽດ"
                                    rules="required|between: 10, 2000"
                                    v-slot="{ errors }"
                            >
                                <ckeditor :editor="editor" v-model="description" ></ckeditor>
                                <p v-if="errors" class="error--text">{{ errors[0] }}</p>
                            </ValidationProvider>
                            <ValidationProvider
                                    mode="lazy"
                                    name="ປະເພດ"
                                    rules="required"
                                    v-slot="{ errors }"
                            >
                                <v-select
                                        v-model="select"
                                        :items="categories"
                                        item-text="name"
                                        item-value="_id"
                                        :menu-props="{ top: true, offsetY: true }"
                                        label="ປະເພດ"
                                        outlined
                                        :error-messages="errors"
                                ></v-select>
                            </ValidationProvider>
                            <v-btn type="submit" class="v-size--x-large success">
                                ເພີ່ມ Portfolio
                            </v-btn>
                        </v-form>
                    </ValidationObserver>
                </v-col>
            </v-card>
            <v-overlay :value="overlay">
                <v-progress-circular color="primary" indeterminate size="70"></v-progress-circular>
            </v-overlay>
        </v-col>
</template>

<script>
    import { mapGetters } from 'vuex'
    import { ValidationProvider, extend } from 'vee-validate';
    import { required } from 'vee-validate/dist/rules';
    import { ValidationObserver } from 'vee-validate'
    import Vue from 'vue';
    import CKEditor from '@ckeditor/ckeditor5-vue';
    import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
    Vue.use( CKEditor );


    extend('required', {
        ...required,
        message: 'ກະລຸນາປ້ອນຂໍ້ມູນໃນ {_field_}'
    })
    extend('between', {
        validate: (value, { min, max }) => {
            const length = value && value.length;
            return length >= min && length <= max;
        },
        params: ['min', 'max'],
        message: 'ຂໍ້ມູນ {_field_} ຕ້ອງມີຄວາມຍາວລະຫວ່າງ {min} ຫາ {max} ຕົວອັກສອນ'
    })

    export default {
        components: {
          ValidationProvider,
            ValidationObserver
        },
        data: () => ({
            editor: ClassicEditor,
            select: '',
            title:'',
            description: '',
            categories: [],
            files: [],
            images: [],
            isDragging: false,
            dragCount: 0,

            items: [
                {
                    text: 'Dashboard',
                    to: { name: 'Dashboard' },
                    exact: true
                },
                {
                    text: 'Post portfolio',
                }
            ],
            overlay: false
        }),
        computed: {
            ...mapGetters([
               'auth'
            ]),
            // dragLeave () {
            //     if(this.images.length <= 0){
            //         this.isDragging = false
            //     }
            //     if(this.images.length >= 1) {
            //         this.isDragging = true
            //     }
            // },
        },
        methods: {
            OnDragEnter(e) {
                e.preventDefault()
                this.dragCount++
                this.isDragging = true
            },
            OnDragLeave(e) {
                e.preventDefault()
                this.dragCount--
                if(this.dragCount <= 0)
                    this.isDragging = false
            },
            onInputChange(e) {
                const files = e.target.files
                Array.from(files).forEach(file => this.addImage(file))
            },
            onDrop(e){
                e.preventDefault()
                e.stopPropagation()
                const files = e.dataTransfer.files
                Array.from(files).forEach(file => this.addImage(file))
            },
            addImage(file) {
                if(!file.type.match('image.*')){
                    this.$swal({
                        title: 'File ຕ້ອງເປັນຮູບພາບ',
                        type: 'error',
                        toast: true,
                        position: 'top-end',
                        showConfirmButton: false,
                        timer: 2000,
                    })
                    return
                }
                this.files.push(file)
                const reader = new FileReader()
                reader.onload = (e) => this.images.push(e.target.result)
                reader.readAsDataURL(file)
            },
            spliceImage(index) {
                this.files.splice(index, 1)
                this.images.splice(index, 1)
            },
            async fetchCategories () {
              try {
                  const results = await this.$axios.get('category/getall')
                  const categories = results.data.categories
                  this.categories = categories
              }  catch (e) {
                  throw e
              }
            },
            async addPortfolio () {
               try {
                   const isValid = await this.$refs.observer.validate()
                   if(isValid) {
                       this.overlay = true
                       if(this.files.length > 0) {
                           const formData = new FormData()
                           this.files.forEach(file=>{
                               formData.append('images', file)
                           })
                           formData.append('title', this.title)
                           formData.append('description', this.description)
                           formData.append('userId', this.auth._id)
                           formData.append('categoryId', this.select)
                           const results = await this.$axios.post('portfolio/post', formData)
                           this.overlay = false
                           this.$router.push({ name: 'Dashboard' })
                           this.$swal({
                               title: 'ເພີ່ມ Portfolio ແລ້ວ',
                               type: 'success',
                               toast: true,
                               position: 'top-end',
                               showConfirmButton: false,
                               timer: 2000,
                           })
                           this.$io.emit('postPortfolio', results.data.portfolio)
                       } else {
                           this.$swal({
                               title: 'ກະລູນາເລືອກຮູບ',
                               type: 'error',
                               toast: true,
                               position: 'top-end',
                               showConfirmButton: false,
                               timer: 2000,
                           })
                       }
                   }
               } catch (e) {
                  throw new Error(e)
               }
            }
        },
        created () {
            this.fetchCategories()
        }

    }
</script>

<style lang="scss" scoped>
    .__card {
        padding-bottom: 50px;
    }
    .__card-title {
        font-size: 30px;
        font-weight: bold;
    }
    .uploader {
        width: 100%;
        background: #2196F3;
        color: #fff;
        padding: 40px 15px;
        text-align: center;
        border-radius: 10px;
        border: 3px dashed #fff;
        font-size: 20px;
        position: relative;
        &.dragging {
            background: #fff;
            color: #2196F3;
            border: 3px dashed #2196F3;
            .file-input label {
                background: #2196F3;
                color: #fff
            }
        }
    }
    .file-input {
        width: 200px;
        margin: auto;
        height: 48px;
        position: relative;
        label, input {
            background: #fff;
            color: #2196F3;
            width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            padding: 10px;
            border-radius: 4px;
            margin-top: 7px;
            cursor: pointer;
        }
        input {
            opacity: 0;
            z-index: -12;
        }
    }
    .i {
        &:hover {
            transform: scale(1.3);
        }
    }
    hr {
        margin: auto;
        width: 95%;
    }
</style>
